import {
    FusePlugin,
    FuseAPIContentType
} from '@nbsolutions/fuse';

export class EchoPlugin extends FusePlugin {
    protected _getID(): string {
        return 'echo';
    }

    public async echo(message: string): Promise<string> {
        let r: Blob = await this._exec('echo', FuseAPIContentType.STRING, message);
        console.log('ECHO RESPONSE', r);
        return await r.text();
    }
}
