import {FuseAPI, FuseAPIContentType, TFuseAPIArgs} from '../FuseAPI';

/**
 * A Fuse API implementation for iOS that uses WKURLSchemeHandler to bridge the JS and Native API calls.
 * 
 * @experimental
 */
export class IOSSchemeFuseAPI extends FuseAPI {
    protected _execute<T>(pluginID: string, method: string, contentType: FuseAPIContentType, args: TFuseAPIArgs): Promise<T> {
        return new Promise<T>((resolve, reject) => {
            let xhr: XMLHttpRequest = new XMLHttpRequest();
            xhr.open('POST', this._createRoute(pluginID, method));
            xhr.onload = () => {
                //TODO: Implement content type resolver
                resolve(xhr.response);
            };

            xhr.onerror = (e) => {
                // TODO: Wrap in custom FuseError
                reject(e);
            };
            
            if (args) {
                xhr.send(JSON.stringify(args));
            }
            else {
                xhr.send();
            }
        });
    }
}
